import React from 'react'
import propTypes from 'prop-types'
import { setAutoFreeze } from 'immer'
import Container from './container'
import { compose } from './utils/func'
import inputable from './public/inputable'
import value from './value'

setAutoFreeze(false)

const DatePicker = compose(
  inputable,
  value,
)(Container)

/**
 * @cn 日期范围选择
 * @desc 蚂蚁交互的日期范围选择器
 * @author leehaochen
 * @date 2020-08-13
 */
class DateRangePicker extends React.PureComponent {
  /**
   * 处理自定义文本，可用于支持多语言
   * @private
   */
  get uiText() {
    const { props } = this
    return Object.assign({}, DateRangePicker.defaultProps.uiText || {}, props.uiText)
  }

  /**
   * render method
   * @return {jsx} rendered nodes
   */
  render() {
    return (
      <DatePicker
        {...this.props}
        uiText={this.uiText}
        range={this.props.range || true}
      />
    )
  }
}

DateRangePicker.defaultProps = {
  uiText: {
    startOfWeek: 1,
    monthValuesShort: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
    weekdayValuesNarrow: ['一', '二', '三', '四', '五', '六', '日'],
    confirm: '确定',
    selectTime: '选择时间',
    selectDate: '选择日期',
  },
  inputable: true,
  zIndex: 1000,
  type: 'date',
  placeholder: ['开始时间', '结束时间'],
  clearable: true,
  autoBlur: false,
  formatForTimeTitle: 'YYYY年 MM月 dd日',
}

DateRangePicker.propTypes = {
  /**
   * 为 true 时，选项弹出层在 DOM 中独立 render
   */
  absolute: propTypes.bool,
  /**
   * 'center' | 'left' | 'right'
   * 值水平布局方式
   */
  align: propTypes.string,
  /**
   * 扩展className
   */
  className: propTypes.string,
  /**
   * 是否可清空
   */
  clearable: propTypes.bool,
  /**
   * 在清空值时抛出是否抛出 undefined
   */
  clearWithUndefined: propTypes.bool,
  /**
   * 面板默认时间，在未选择日期时生效
   */
  defaultPickerValue: propTypes.oneOfType([propTypes.number, propTypes.string, propTypes.instanceOf(Date)]),
  /**
   * 默认值。如果 defaultValue 和 format 类型不一致，会执行一次 format，并触发 onChange 事件返回 format 后的值
   */
  defaultValue: propTypes.arrayOf(propTypes.any),
  /**
   * 选择日期时默认的时间, 格式为: 'HH:mm:ss'
   */
  defaultTime: propTypes.arrayOf(propTypes.any),
  /**
   * 禁用
   */
  disabled: propTypes.oneOfType([propTypes.func, propTypes.bool]),
  /**
   * 禁用指定 Time。
   */
  disabledTime: propTypes.oneOfType([propTypes.func, propTypes.bool]),
  /**
   * 不同type对应的默认值
   * 'date': 'yyyy-MM-dd'
   * 'time': 'HH:mm:ss'
   * 'week': 'RRRR II'
   * 'month': 'yyyy-MM'
   * 'datetime': 'yyyy-MM-dd HH:mm:ss'
   */
  format: propTypes.string,
  /**
   * 对选中时间进行格式化
   */
  formatResult: propTypes.func,
  /**
   * 小时选项步长
   */
  hourStep: propTypes.number,
  /**
   * 可选最大值
   */
  max: propTypes.oneOfType([propTypes.string, propTypes.number, propTypes.instanceOf(Date)]),
  /**
   * 可选最小值
   */
  min: propTypes.oneOfType([propTypes.string, propTypes.number, propTypes.instanceOf(Date)]),
  /**
   * 分钟选项步长
   */
  minuteStep: propTypes.number,
  /**
   * 值改变回调函数
   */
  onChange: propTypes.func,
  /**
   * 值改变回调，有别于 onChange, onPickerChange会在每项值改变的时候执行
   */
  onPickerChange: propTypes.func,
  /**
   * 占位文字
   */
  placeholder: propTypes.arrayOf(propTypes.string),
  /**
   * 值改变回调函数范围跨度，单位 秒，
   */
  range: propTypes.number,
  /**
   * 秒选项步长
   */
  secondStep: propTypes.number,
  /**
   * 最外层扩展样式
   */
  style: propTypes.object,
  /**
   * 时间类型 'date' | 'datetime'
   */
  type: propTypes.string,
  /**
   *  值
   */
  value: propTypes.arrayOf(propTypes.oneOfType([propTypes.string, propTypes.number, Date])),
  /**
   * 选择面板z-index
   */
  zIndex: propTypes.number,
  /**
   * 文本
   */
  uiText: propTypes.shape(),
  /**
   * 选择时间的时候上面显示的日期格式
   */
  formatForTimeTitle: propTypes.string,
  /**
   * 是否可编辑
   */
  inputable: propTypes.bool,
  /**
   * 内嵌标题
   */
  innerTitle: propTypes.string,
  /**
   * 选完日期范围后是否自动失焦
   */
  autoBlur: propTypes.bool,
}


export default DateRangePicker
